import { BrowserRouter as Router } from 'react-router-dom';
import './App.css';
import Views from './views';
import AOS from "aos";
import "aos/dist/aos.css";
import { useEffect } from 'react';

function App() {

  useEffect(() => {
    AOS.init({
      duration:2000
    });
    AOS.refresh();
  }, [])

  return (
    <Router>
      <Views/>
    </Router>
  );
}

export default App;
