import styled from "styled-components"
import { Container, Heading, List, Row } from "./layout"

export default function Footer () {
    return (
        <Wrapper>
            <Container>
                <Row justifyContent="space-between" wrap="wrap">
                    <Heading>Jett</Heading>
                    <List type="none" direction="flex" gap="30px" wrap="wrap">
                        <li>Contact us</li>
                        <li>Services</li>
                        <li>Pricing</li>
                    </List>
                </Row>
            </Container>
        </Wrapper>
    )
}

const Wrapper = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 0px 20px;
`