import styled from 'styled-components';
import { Button, Container, Heading, List, Row } from './layout';
import { BiMenuAltRight } from 'react-icons/bi';
import { AiOutlineClose } from 'react-icons/ai';
import { useRef } from 'react';

export default function Header() {

    const menuRef = useRef();

    const menuShow = () => {
        menuRef.current.style.maxWidth = "100%";
    }

    const hideMenu = () => {
        const flag = window.matchMedia("(max-width:680px)").matches;
        if(flag) {
            menuRef.current.style.maxWidth = "0%";
        }
    }


    return (
        <Wrapper>
            <Container>
                <Row justifyContent="space-between" wrap="wrap">
                    <Heading>Jett</Heading>
                    <List ref={menuRef} type="none" direction="flex" wrap="wrap" gap="30px" alignItems="center" className='jett-menu'>
                        <AiOutlineClose className='close-menu' onClick={() => hideMenu()} />
                        <li>
                            <a href='#jett-hero' onClick={hideMenu}>
                                Home
                            </a>
                        </li>
                        <li>
                        <a href='#jett-plan' onClick={hideMenu}>
                                Pricing
                        </a>
                        </li>
                        <li>
                        <a href='#jett-vison-goal' onClick={hideMenu}>
                            Services
                        </a>
                        </li>
                        <li>
                        <a href='#jett-contacts' onClick={hideMenu}>
                            Contacts
                            </a>
                            </li>
                        <li>
                            <Button bg="#FF6600" color='white'>
                                Book a Call
                            </Button>
                        </li>
                    </List>
                    <BiMenuAltRight className='jett-menu-icon' onClick={() => menuShow()} />
                </Row>
            </Container>
        </Wrapper>
    )
}

const Wrapper = styled.header`  
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 0px 20px;
    position: sticky;
    top: 0px;
    background-color: white;
    z-index: 6;
    .close-menu {
        position: absolute;
        top: 50px;
        right: 50px;
        font-size: 18px;
        cursor: pointer;
        display: none;
        @media screen and (max-width:680px) {
            display: block;
        }
    }
    .jett-menu {
        transition: all 0.3s;
        @media screen and (max-width:680px) {
            position: fixed;
            z-index: 5;
            background-color: white;
            flex-direction: column;
            top: 0px;
            left: 0px;
            right: 0px;
            bottom: 0px;
            max-width: 0px;
            overflow: hidden;
            padding-top: 120px;
        }
    }
    .jett-menu-icon {
        color: black;
        display: none;
        font-size: 24px;
        cursor: pointer;
        @media screen and (max-width:680px){
            display: block;
        }
    }
`