import styled from "styled-components";

export const Row = styled.div`
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    align-items: ${props => props.alignItems ? props.alignItems : "center"};
    justify-content: ${props => props.justifyContent};
    flex-wrap: ${props => props.wrap};
    gap: ${props => props.gap};
`

export const Column = styled.div`
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: ${props => props.alignItems};
    max-width: ${props => props.width};
    gap: ${props => props.gap};
    flex: ${props => props.flex};
`

export const Container = styled.div`
    max-width: 1680px;
    flex: 1;
    box-sizing: border-box;
`

export const Heading = styled.h1`
    font-family: Montserrat;
    font-size: ${props => props.size};
    color: #0B1B35;
    text-align: ${props => props.textAlign};
`

export const List = styled.ul`
    padding: 0px;
    margin: 0px;
    list-style: ${props => props.type};
    display: ${props => props.direction};
    font-family: Montserrat;
    font-weight: 500;
    flex-wrap: ${props => props.wrap};
    gap: ${props => props.gap};
    cursor: pointer;
    align-items: ${props => props.alignItems};
    a{
        text-decoration: none;
        color: black;
    }
`

export const Button = styled.button`
    padding: 15px 30px;
    border: none;
    border-radius: 10px;
    font-family: Montserrat;
    font-weight: 500;
    cursor: pointer;
    background-color: ${props => props.bg};
    color: ${props => props.color};
    max-width: ${props=>props.width};
`

export const Text = styled.p`
    font-family: ${props => props.font ? props.font : "Open Sans"};
    margin: ${props => props.margin ? props.margin : "0px"};
    line-height: 32px;
    font-size: ${props => props.fontSize ? props.fontSize : "18px"};
    color: ${props => props.color ? props.color : "#0B1B35"};
    text-align: ${props => props.textAlign};
    font-weight: ${props => props.weight};
`

export const Tag = styled.span`
    font-family: Montserrat;
    font-size: 14px;
    color: #8A898E;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 0.4rem;
`

export const Card = styled.div`
box-shadow: 0px 100px 80px rgba(0, 0, 0, 0.07), 0px 41.7776px 33.4221px rgba(0, 0, 0, 0.0503198), 0px 22.3363px 17.869px rgba(0, 0, 0, 0.0417275), 0px 12.5216px 10.0172px rgba(0, 0, 0, 0.035), 0px 6.6501px 5.32008px rgba(0, 0, 0, 0.0282725), 0px 2.76726px 2.21381px rgba(0, 0, 0, 0.0196802);
border-radius: 28px;
padding: 50px 20px;
max-width: ${props => props.width};
display: flex;
flex-direction: column;
align-items: center;

`

export const Grid = styled.div`
    display: grid;
    grid-template-columns:repeat(${props => props.column}, minmax(0, 1fr));
    gap: ${props => props.gap};
    grid-template-rows: repeat(${props => props.row}, minmax(0, 1fr));
`

export const PlanBox = styled.div`
    border: 1.5px solid #EBECED;
    border-radius: 16px;
    padding: 30px;
    max-width: ${props => props.width};
`

export const Splitter = styled.div`
    border: 1px solid #EBECED;
`

export const BorderButton = styled.button`
    border: 2.5px solid #EBECED;
    border-radius: 8px;
    padding: 15px 30px;
    font-family: Montserrat;
    background-color: transparent;
    cursor: pointer;
`
export const TeamCard = styled.div`
border: 1.5px solid #EBECED;
border-radius: 16px;
padding: 30px;
flex: 1;
width: 310px;
@media screen and (max-width:450px) {
    width: 100%;
}
`

export const Input = styled.input`
    background: #FFFFFF;
box-shadow: 0px 100px 80px rgba(0, 0, 0, 0.07), 0px 41.7776px 33.4221px rgba(0, 0, 0, 0.0503198), 0px 22.3363px 17.869px rgba(0, 0, 0, 0.0417275), 0px 12.5216px 10.0172px rgba(0, 0, 0, 0.035), 0px 6.6501px 5.32008px rgba(0, 0, 0, 0.0282725), 0px 2.76726px 2.21381px rgba(0, 0, 0, 0.0196802);
border-radius: 49px;
border: none;
height: 50px;
outline: none;
text-indent: 15px;
font-family: Montserrat;
border: 1px solid #EBEAED;
`