import styled from "styled-components";
import { HashLoader } from "react-spinners";

export default function Loader () {
    return (
        <Wrapper>
            <HashLoader color="#FF6600" />
        </Wrapper>
    )
}


const Wrapper = styled.div`
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    z-index: 10;
    background-color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
`